* {
    margin: 0;
    padding: 0;
    overflow: visible;
    font-family: 'Ubuntu',
    sans-serif;
    
}

.flex{
    display: flex;
}
.space-between{
    justify-content: space-between;
}

.space-around {
    justify-content: space-around;
}

.space-evenly {
    justify-content: space-evenly;
}

.justify-center {
    justify-content: center;
}

.justify-start {
    justify-content: flex-start;
}
.justify-end {
    justify-content: flex-end;
}
.align-center {
    align-items: center;
}

.flex-column {
    flex-direction: column;
}

.bold {
    font-weight: bold;
}

.font-normal{
    font-weight: normal;
}

.text-center {
    text-align: center;
}

.max-width {
    width: 100%;
}
.max-height {
    height: 100%;
}

.relative {
    position: relative;
}
.absolute {
    position: absolute;
}

.absolute-bottom {
    bottom:0;
}


.home-page {
    width: 100vw;
    height: 100vh ;
    position: relative;
}


.white-back {
    background-color: white;
}

.black-text {
    color: black;
}

.blue-text {
    color: #064ACB;
}

.white-text {
    color: white;
}

.grey-text {
    color: grey;
}


.landing-logo .logo {
    width: 150px ;
    height: 150px ;


}

.palette_input {
    width: 80px !important;
    padding-right: 0 !important;
    padding-left: 5px !important;
    

}

.margin-top20 {
    margin-top: 50px;
}
.margin-top10 {
    margin-top: 10px;
}

.margin-right20 {
    margin-right: 20px;
}
.margin-left20 {
margin-left: 20px;
}

.margin-bottom10 {
    margin-bottom: 10px;
}

.margin-bottom20 {
    margin-bottom: 20px;
}

.cancel-action {
    color: #474744 !important;
}
.btn {
    height: 45px;
    border-radius: 5px;
    padding-inline: 30px;
    color: white;
    width: fit-content;
    cursor: pointer;
}

.cursor {
    cursor: pointer;
}

.orange-back {
    background-color: #FF731D;
}

.blue-back {
    background-color: #064ACB;
}

.black-back {
    background-color: #474744;
}

.colored-back-text {
    width: fit-content;
    padding-inline: 20px;
    border-radius: 10px;
    height: 35px;
    opacity: 0.7;
}
.main-text {
    font-size: 65px;
}

.main-form-text {
    font-size: 30px;
}

.input-container {
    width: calc(100% - 4px);
}

.overlay {
    position: fixed;
    top: 0;
    z-index: 5;
    background-color: rgba(0, 0, 0, 0.35);
}

a {
    text-decoration: none;
    color: grey;
}


.big-input {
    height: 200px;
}
input,
select,
option {
    width: calc(100% - 60px);
    height: 40px;
    border-radius: 5px;
    border: #474744 solid 2px;
    margin-top: 10px;
    padding-inline: 30px;
    font-weight: bold;
}

select {
    width: 100%;
}


.form-actions {
    width: 100%;
}
.shadow {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 2px 5px;
}

.icon {
    width: 55px;
    height: 55px;
}

.small-img {
    width: 35px;
    height: 35px;
}

.medium-img {
    width: 45px;
    height: 45px;
}

.big-img {
    width: 25px;
    height: 25px;
}



.nav {
    display: flex;
}

/* page style */

.header {
    width: calc(100% - 200px);
    padding-inline: 100px;
    position: fixed;
    z-index: 5;
    
}


.padding-inline100 {
    padding-inline: 100px;
    width: calc(100% - 200px);
}


.home-section-left,
.home-section-right {
    width: 50%;
    position: relative;
}

.home-section {
    height: calc(100vh - 70px);
}

.home-section > * {
    position: relative;
    top: 20px;
}

.croped-container {
    width: 55%;
    height: 60vh;
    display: flex;
    
    justify-content: flex-end;
    clip-path: polygon(0 23%, 100% 0, 100% 100%, 0% 100%);
    border-radius: 10px;
    overflow: hidden;
}


.home-section-right > img {
    transform: scaleX(-1);
    position: absolute;
    right: 20px;
    bottom: 200px;
    bottom: 0;
}

.home-section-right .pop-notification:first-child {
    width: 100px;
    height: 150px;
    border-radius: 10px;
    z-index: 2;
    right: -50px;
    top: 50px;
    
}


.home-section-right .pop-notification:nth-child(2) {
    width: 120px;
    height: 70px;
    border-radius: 10px;
    z-index: 2;
    right: 40px;
    bottom: -50px;

}

.home-section-right .pop-notification:nth-child(3) {
    width: 70px;
    height: 70px;
    border-radius: 10px;
    z-index: 2;
    right: 340px;
    bottom: 20px;


}

.pop-notification:nth-child(3) img {
    position: relative;
    top: 4px;
}



.page-container {
    overflow-x: hidden;
}

.order-page  , 
.contact-page,
.about-page{
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden ;

}

.contact-page {
}

.order-page > *{
    position: relative;
    top: 10px;
}
.right-order-form {
    position: absolute;
    right: 300px;
    width: 30%;
}
.left-order-form>*,
.right-order-form > *{
    position: relative;
    top: 50px;
}

.vl {
    position: absolute;
    width: 2px;
    height: 50vh;
    background-color: #474744;
    border-radius: 5px;
    top: calc(50% );
    left: calc(50% - 100px);
    transform: translate(-50% , -50%);

}

.vl p {
    position: relative;
    bottom: 25px;
}

.signup-container {
    width: 50%;
    height: 50%;
    background-color: white;
    border-radius: 5px;
}

.signin-overlay {
    width: calc(100% - 60px);
    max-width: 1000px;
    height: calc(100% - 60px);
    border-radius: 5px;
}

.signin-overlay .form-container {
    width: calc(100% - 60px);
    height: calc(100% - 60px);
}

.signup-slider {
    overflow: hidden;
}

.contact-left {
    width: calc(60% - 200px);
    height: 100%;
    background-color: #FF731D;
    position: relative;
}

.contact-left-overlay {
}
.contact-right {
    width: 40%;
    height: 100%;
}

.social-container img {
    margin-right: 15px;
}

.contact-right .form-container,
.contact-right >p {
    width: 80%;
}



.about-page {
}

.about-text-back p{
    opacity: 0.1;
    font-size: 20.5rem !important;
}


.login-loading-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 30px !important;
    height: 30px !important;
    transform: translate(-50% , -50%);
}

.popup-text {
    position: absolute;
    bottom: -30px;
}

.confirme-overlay {
    
}


.order-tabs {
    margin-top: 30px;
}

.order-tab {
    width: 140px;
    height: 40px;
    margin-right: 20px;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
    border: none;

}


.order-tab img {
    position: absolute;
    right: 5px;
    width: 20px;
}
.add-order-tab {
    cursor: pointer;
}

.add-order-tab img{
    width: 25px;
}

.orders-page {
    width: 100% !important;
}

.order-forms-tab {
    width: 95%;
    height: 80%;
    position: absolute;
    bottom: 0px;
}

.order-form-tab {
    width: 100%;
    height: 100%;
    background-color: transparent;
}

.order-form-tab .form-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.order-form-tab .form-actions {
    margin-top: 10px;
}

.input-groupe {
}

.desc-input input{
    width: calc(100% - 40px) !important;
}


.order-details-container {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.35);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    z-index: 50;
    position: absolute;
    top: 0;
    left: 0;
    font: bold;
}

.select-transporter-overlay {
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0 , 0.4);
    border-radius: 5px;
    left: 0%;
    top: 0;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.select-transporter-container {
    width: 50%;
    height: 250px;
    background-color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    text-align: center;
}

.select-transporter-container > p {
    margin-top: 20px;
}

.select-transporter-container > p:first-child {
    color: gray;
    font-size: larger;
}

.select-transporter-container span {
    color: #362FD9;
}

.select-transporter-container .conf-actions {
    display: flex;
    margin-top: 40px;
}

.conf-action {
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #362FD9;
    cursor: pointer;
    width: 100px;
    height: 40px;
    padding: 5px;
    margin: 5px;


}

.select-transporter-container .conf-action:last-child {
    background-color: transparent;
    color: grey;
}

@media only screen and (max-width: 700px) {
    .select-transporter-container {
        width: 250px;
        height: 200px;
        position: relative;
        left: 30px;    
        background-color: white;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px;
        text-align: center;
    }   
}
