.client-body {
    height: calc(100vh - 90px);
    width: calc(100vw - 200px);
    overflow: hidden;
    
}

.dashboard-home {
height: calc(100vh - 40px);
width: calc(100vw - 40px);
padding: 20px;
overflow: hidden !important;
display: flex;
justify-content: space-between;
background-color: #f5f5f5;

}


.loading-icon {
    width: 200px;
    height: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;

    flex-direction: column-reverse;
    text-align: center;
    z-index: 1;
}

.loading-icon img {
    width: 50px;
    height: 50px;
}



.side-nav {
    width: 60px;
    height: calc(100%);
    background-color: #FF731D;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 30;
}

.nav-item {
    width: 45px;
    height: 45px;
    border-radius: 10px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    background-color: rgba(255, 255, 255, 0.2);

}

.nav-item img:last-child {
    width: 30px;
    height: 30px;
}

.side-nav .nav-item:first-child {
    background-color: rgba(255, 255, 255, 01);
    
}

 .side-nav .nav-item:first-child img {
    width: 35px;
    height: 35px;
}
.side-nav .nav-item:last-child {
    position: absolute;
    bottom: 20px;
}

.nav-item:last-child img {
    width: 25px;
    height: 25px;
    transform: rotate(180deg);
}
.selected-background {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.2);
}


.hr {
    width: 100%;
    height: 1px;
    background-color: white;
    margin-top: 20px;
}



.pages-container {
    height: 100%;
    width: calc(100% - 100px);
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 5;
    color: #362FD9 !important;
    font-weight: bold;
}


.client-stat-container {
    height: 25%;
    border-radius: 5px;
    flex-direction: row;
}

.order-action-btn {
    min-height: 50px;
    width: 200px;
    border-radius: 5px;
    cursor: pointer;
}


.client-stat-circle {
    width: 80px;
    height: 80px;
    border-radius: 100%;
    border: #362FD9 6px solid;

}




/*
copied style
*/

.left-orders {
    width: calc(70% - 100px);
    height: 100%;
    padding-inline: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    position: relative;
    font-weight: bold;
}

.dash-page-title p {
    font-size: 13px;
    opacity: 0.8;
}

.dash-page-title p+p {
    opacity: 1;
    font-size: 18px;
    color: #362FD9;
}

.orders-filters {
    display: flex;
    width: 80%;
    font-size: 15px;
    align-items: center;
    margin-top: 20px;
    opacity: 0.8;
    color: #212A3E;
}

.orders-filters>div {
    cursor: pointer;
    position: relative;
    margin-right: 80px;
    min-width: fit-content;
}

.selected-order-filter {
    position: absolute;
    width: 100%;
    background-color: #212A3E;
    bottom: -5px;
    height: 3px;
    border-radius: 5px;
}


.orders-filters img {
    width: 20px;
    height: 20px;
    top: 0;
}



.orders-list {
    width: 100%;
    height: 85%;
    max-height: 85%;
    overflow: auto;
    position: absolute;
    bottom: 0;
    overflow-x: hidden;
}

.order-list-item {
    width: calc(100% - 40px);
    height: 60px;
    padding-inline: 20px;
    border-radius: 5px;
    background-color: white;
    position: relative;
    margin-top: 7.5px;
    display: flex;
    justify-content: space-evenly;

}

.order-list-item>* {
    width: calc(100% / 8);
    height: 100%;
    display: flex;
    justify-content: start;
    align-items: center;
    text-align: start;
}

.order-list-item>p:first-child {
    width: calc(100% / 8 - 40px);
}

.order-list-item>p:nth-child(3) , .order-list-item>p:nth-child(5) {
    width: calc(100% / 8 + 20px);
}



.action {
    position: absolute;
    right: 10px;
    width: 20px;
    height: 100%;
    cursor: pointer;
}

.order-actions-container {
    display: flex;
    justify-content: center;
    align-items: center;
}


.order-action {
    padding-inline: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    width: fit-content;
    height: fit-content;

    border-radius: 5px;
    margin-right: 20px;
    color: white;
    background-color: #212A3E;
    cursor: pointer;
}

.bkBlue {
    background-color: #362FD9;
}

.bkYellow {
    background-color: #FFC436;
}

.bkgreen {
    background-color: green;
}

.bkred {
    background-color: red;
}

.state-container {
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    display: flex;
    justify-content: start;
    align-items: center;
}

.stat-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    border-radius: 100%;
}

.order-list-item img {
    transform: rotate(90deg);
}

.filer-components>p {
    width: 100%;
    height: 100%;
}

.filter-select {
    position: absolute;
    width: 150px;
    top: 120%;
    left: 0;
    border-radius: 5px;
    height: fit-content;
    z-index: 2;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

}

.filter-options {
    height: 50px;
    margin-top: 10px;
    width: calc(100% - 40px);
    display: flex;
    align-items: center;
    color: #212A3E;
    padding-inline: 20px;
    border-bottom: 1px white solid;
    display: flex;
    justify-content: space-between;
}


.filters-container {
    display: flex;
    margin-top: 20px;
}

.filer-components {
    width: fit-content;
    height: fit-content;
    padding: 10px;
    padding-inline: 15px;
    border-radius: 5px;
    margin-right: 10px;
    font-size: 15px;
    cursor: pointer;
    position: relative;
    background-color: white;
}


.right-orders {
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
}



.dashboard-orders {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color: #212A3E;
    display: flex;
    justify-content: space-between;
}

.logout-conf , .order-conf-overlay-content{
    width: 30%;
    height: 250px;
    border-radius: 5px;
}


.hidden-msg {
    position: absolute;
    left: 0;
}

.email-conf-form {
    position: absolute;
    left: 100%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.email-conf-form .input-container {
    width: 80%;
}


.back-btn {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotate(180deg);
    cursor: pointer;
}

.back-btn img {
    width: 20px;
    height: 20px;
}





/*Tabs Style*/

/* styles.css */




  
  .container {
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  button {
    margin-right: 10px;
    margin-bottom: 10px;
  }
  
  .tabForm {
    width: 70%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 10px;
    position: absolute;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
    
  }
  .tabForm> :nth-child(2) {
    grid-row-start: 1;
    grid-column: 1 / span 2;
  }

  .tabForm> :nth-child(3) {
    grid-row-start: 1;
    grid-column: 3 / span 2;
  }

  .tabForm> :nth-child(4) {
    grid-row-start: 2;
    grid-column: 1 / span 2;
  }

  .tabForm> :nth-child(5) {
    grid-row-start: 2;
    grid-column: 3 / span 2;
  }

  .tabForm> :nth-child(6) {
    grid-row-start: 3;
    grid-column: 1 / span 2;
  }

  .tabForm> :nth-child(7) {
    grid-row-start: 3;
    grid-column: 3 / span 2;
  }

  .tabForm> :nth-child(8) {
    grid-row-start: 4;
    grid-column: 1 / span 2;
  }

  .tabForm> :nth-child(9) {
    grid-row-start: 4;
    grid-column: 3 / span 2;
  }

  .tabForm> :nth-child(10) {
    grid-row-start: 5;
    grid-column: 1 / span 2;
  }

  .tabForm> :nth-child(11) {
    grid-row-start: 5;
    grid-column: 2 / span 2;
    width: 200px;
    z-index: 5;
  }

  .tabForm> :nth-child(12) {
    grid-row-start: 5;
    grid-column: 3 / span 2;
  }

  .tabForm> :nth-child(13) {
    grid-row-start: 6;
    grid-column: 1 / span 4;
  }

  .tabForm> :nth-child(14) {
    grid-row-start: 7;
    grid-column: 1 / span 4;
  }
  
  label {
    font-weight: bold;
    color: #212A3E;
  }
  

  
  button[type="submit"] {
    grid-column: span 2;
    border: none;
  }
  
  
  
  .tabForm > div:nth-child(10)  {
    display: flex;
    flex-direction: column;
    position: relative;

  }



  .tabForm > div:nth-child(10) .radios-container + div > input{
    
    height: 40px !important;
    
  }

  .tabForm > div:nth-child(10) .radios-container + div {
    
    position: relative;
    bottom: 18px;
    
  }


  .tabForm > div:nth-child(10){
    
    margin-bottom: 20px;
    
  }
  .tabForm > div:nth-child(10) label {
    display: flex;
    align-items: flex-end;
    justify-content: flex-star !important;
    width: 100%;
    position: relative;
  }

  .tabForm > div:nth-child(10) input {
    display: flex;
    align-items: center;
    justify-content: flex-star !important;
    width: 20px;
    height: 20px;
    margin-right: 30px;
  }

.radios-container {

    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: 10px;

}
  


  .tabform-main-text {
    color: #362FD9;
    font-size: 25px;
    text-align: center;
    width: 100%;
    top: -60px;
    color: #212A3E;
  }

  .orderTabPage {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
  }

  .disabled {
    background-color: #caced7 !important;
  }


  
@media only screen and (max-width: 1302px) {

    .side-nav {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 20;
    }

    .order-conf-overlay {
        z-index: 21 !important;
    }
    .orders-list {
    }
    
    .right-orders {
        display: none;
    }

    .left-orders {

        width: calc(100% - 80px);
        position: absolute;
        left: 70px;
    }

    .stat-circle {
        min-width: 35px;
        min-height: 35px;
    }
}
  


@media only screen and (max-width: 980px) {

    .orders-filters {
        overflow-x: auto;
        width: 100%;
        padding: 10px 0;
    }

    .orders-filters::-webkit-scrollbar {
        display: none;
      }

    .pages-container {
        width: calc(100% - 20px);
        padding: 0;
    }

    .left-orders {
        width: calc(100% - 20px);
        left: 50px;
        padding-inline: 0;
    }

    .tabForm {
        width: 100%;
    }

    .orders-list {
        width: 100%;
    }

    .orders-list>.order-list-item:first-child {
        position: absolute;
        opacity: 0;
        pointer-events: none;
        display: none;
    }

    .order-list-item {
        width: 100%;
        padding: 20px;
        margin-top: 30px;
        box-sizing: border-box;
        display: grid;
        grid-auto-flow: row;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-gap: 15px;
        height: unset;
    }

    .order-list-item.order-actions-container {
        display: flex;
        margin-bottom: 10px;
        box-sizing: border-box;
    }

    .order-list-item.order-actions-container .order-action {
        padding-inline: unset;
        padding-top: unset;
        padding-bottom: unset;
        padding: 10px 15px;
    }

    .order-list-item>p {
        padding-left: 100px;
        position: relative;
    }

    .order-list-item>p:before {
        width: 100px;
        position: absolute;
        left: 0;
        opacity: 0.7;
    }

    .order-list-item>p:first-child:before {
        content: "id";
    }

    .order-list-item>p:nth-child(2):before {
        content: "client";
    }

    .order-list-item>p:nth-child(3):before {
        content: "date";
    }

    .order-list-item>p:nth-child(4):before {
        content: "from";
    }

    .order-list-item>p:nth-child(5):before {
        content: "to";
    }

    .order-list-item>p:nth-child(6):before {
        content: "payment";
    }

    .order-list-item>p:nth-child(7):before {
        content: "price";
    }

    .order-list-item>p:nth-child(8):before {
        content: "state";
    }

    .order-conf-overlay-content , .logout-conf  {
        width: 300px;        
    }
       
}


@media only screen and (max-width: 681px) {

    .order-tabs {
        width: 100%;
        overflow: scroll;
        justify-content: flex-start;
    }

    .order-tab {
        min-width: 140px ;
    }
    .tabForm {
        display: flex;
        flex-direction: column;
        top: 200px;
        left: 0px;
        transform: none;
        width: calc(100% - 10px);
    }

  

    .left-orders {
        overflow-y: scroll;
        overflow-x: hidden;
    }

       
}



@media only screen and (max-width: 450px) {

    .order-conf-overlay-content, .logout-conf {
        width: 250px;
    }
    
}

.order-conf-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);


}

.order-conf-overlay-content, .logout-conf {
    height: 200px;
    position: relative;
    left: 30px;
}


.hidden-text {
    margin-bottom: 70px;
    color: #212A3E !important;
}