
* {
    
}

body {
    overflow: hidden;
}
.landing-colored-back {
    width: 100vw;
    height: 100vh;
    overflow: hidden !important;
}

.landing-loading {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;

}

.landing-loading img {
    width: 150px;
    height: 150px;
}
.landing-loading-line {
    width: 60%;
    height: 4px;
    background-color: rgb(183, 176, 176);
    border-radius: 5px;
    margin-top: 20px;
}

.landing-loading-line-anim {
    background-color: #202d57;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    top: 0;
}
.landing-back-white {
    width: calc(100vw - 40px);
    height: calc(100vh - 40px);
    background-color: white;
    position: absolute;
    top: 0;
    border-bottom-right-radius: 20px;
    overflow: hidden;
}

.landing-back {
    position: absolute;
    overflow: hidden;
}

.top-right-orange {
    right: 0;
    width: 200px;
    height: 100px;
    border-bottom-left-radius: 20px;
    background-color: #ea531e;
    z-index: 2;
}

.top-right-blue {
    right: 0;
    width: 300px;
    height: 50px;
    border-bottom-left-radius: 20px;
    background-color: #202d57;
    z-index: 2;
}

.bottom-left-blue {
    left: 0;
    bottom: 0;
    width: 60vw;
    height: 90px;
    border-top-right-radius: 20px;
    background-color: #202d57;
    z-index: 2;
}

.bottom-left-orange {
    left: 0;
    bottom: 0;
    height: 70px;
    border-top-right-radius: 20px;
    background-color: #ea531e;
    z-index: 3;
    width: 70%;
    display: flex;
    justify-content: space-between;
    padding-inline: 20px;
    
}


.landing-container {
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 8;
    overflow: hidden;
}

.landing-logo {
    position: absolute;
    left: 25px;
    top: -25px;
}


.landing-logo img {
    width: 100px;
    height: 100px;
}

.landing-header {
    width: 100vw;
    position: relative;
    top: 100px;
    
}

.landing-nav-item {
    margin-right: 30px;
    
    margin-left: 30px;
    width: 80px;
    text-align: center;
    position: relative;
    cursor: pointer;
}

.selected-nav {
    position: absolute;
    bottom: -7px;
    width: 100%;
    height: 2px;
    background-color: #ea531e;
    border-radius: 20px;
}

.landing-infos {
    width: calc(40vw - 60px);
    padding: 30px;
    position: absolute;
    top: 50%;
    margin-left: 100px;
    transform: translateY(-50% );
    
}

.landing-infos .btn {
    background-color: #202d57;
    min-height: 50px !important;
}

.landing-infos
.colored-back-text {
    background-color: #ea531e;
    opacity: .8;
    
    min-height: 35px !important;
}

.landing-login {
    position: absolute;
    right: 40px;
    
    top: 50%;
    transform: translateY(-50%);
    width: calc(50vw - 70px);
    
    padding: 30px;
}



.landing-login .form-container {
    width: 65%;
}

.landing-main-text {
    font-size: 30px;
    margin-top: 10px;
    margin-bottom: 20px;
}

.landing-main-text + .grey-text {
    width: 110%;
}

.landing-login-text {
    font-size : 25px;
}

.contact-item {
    margin-left: 10px;
    margin-top: 10px;
}

.contact-item > p {
    width: 100px;
}

.contact-item  img{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.landing-signUp {
    position: relative;
    overflow: hidden;
    z-index: 10;
}
.back-img {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 1;

}




.close-btn{
    position: absolute;
    top: 10px;
    right: 15px;
    width: 25px;
    height: 25px;
    cursor: pointer;
}


.landing-colored-back .landing-signUp {
    background-color: white;
}

.landing-colored-back .landing-signUp .back-img {
    opacity: 0.4;
}

.landing-colored-back .overlay {
    z-index: 20;
}

.tracking-form {
    margin-top: 20px;
}


.tracking-form .input-container {
    width: 60%;
    margin-right: 30px;
}

.tracking-res-item {

    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: rgb(183, 176, 176);
    color: White;
    margin-right: 20px;
    margin-top: 20px;
}


.nav {
    z-index: 9;
    height: 30%;
    top: 20px;
}
.tracking-container img {
    width: 70px;
    height: 70px;
}

.big-form-text {
    margin-bottom: 10px;
}
.tracking-container .landing-login .grey-text,
.tracking-container .landing-login img {
    display: none;
}

.about-section {
    width: 100%;
    height: 100%;
    padding: 40px;
    
    box-sizing: border-box;
    
    color: #353535;
}

.about-section img {
    width: 90px;
    height: 90px;
}

.right-about img {
    width: 50px;
    height: 50px;
    margin-top: 20px;
    margin-right: 10px;
}


.left-about {
    width: 50%;
    margin-right: 100px;
    height: 30%;
}
.about-section p {
    line-height: 25px;
}

.right-about {
    
    height: 30%;
    position: relative;
    top: 85px;
    text-align: center;
}

.service-item  {
    width: 20%;
    text-align: center;
    height: 200px;
    color: #353535;
    margin-right: 20px;
    margin-left: 20px;
    line-height: 20px;
}

.service-item img {
    margin-bottom: 20px;
}

.section-title {
    top: 200px;
}


.service-responsive {
    display: none;
}

.services-container {
    width: 100vw;
}

#signup {
    display:none;
}

  @media only screen and (max-width: 1076px) {
    
    #signup{
        width:100%;
        margin-top:15px;
        box-sizing: border-box;
        display: flex;
        background-color:#066593;
        color:white;
    }

    .section-title {
        display: none;
    }
    .nav {
        top: 80px;
    }
    .landing-infos {
        display: none;
    }

    .landing-login {
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        left: 50%;
        top: 60%;
        transform: translate(-50% , -50%);
    }

    .login-text {
        align-items: center;
        text-align: center;
    }


    .landing-login .input-container {
        width: 100%;
    }

    .bottom-left-orange {
        display: none;
    }

    .tracking-container .landing-login .grey-text,
.tracking-container .landing-login img {
    display: flex;
}

.landing-container .form-container {
    width: 90%;
}

.services-container {
    display: grid;
    grid-template-columns: 1Fr 1fr ;
    grid-gap: 10px;
    position: relative;
    top: 50px;

}

.service-item {
    width: 90%;
    height: 200px;
    padding: 30px;
    
    box-sizing: border-box;
    
    position: relative;
}


  }


  @media only screen and (max-width: 700px) {
    .about-section {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .signup-slider {
        margin-top:40px;
        margin-bottom:40px;
        overflow:auto;
    }

    .input-groupe {
        flex-direction: column;
        gap: 15px;
    }

    .left-about img {
        display: none;
    }

    .landing-colored-back {
        background-color: white !important;
    }

    .bottom-left-blue {
        height: 30px;
    }
    .about-section > div {
        height: fit-content;
        margin: 0;
    }

    .left-about , .right-about {
        width: 100%;
        align-items: center;
        text-align: center;
    }

    
    .left-about {
        position: relative;
        top: 70px;
    }

}

  
  @media only screen and (max-width: 500px) {

    

    .login-text .landing-login-text{
        font-size: 18px;
    }

    .login-text p{
        font-size: 14px;
    }


    .landing-nav-item {
        width: fit-content;
    }
    
    .left-about {
    }
    .bottom-left-orange {
        display: none;
    }
    

    .service-section {
    }

    .service-responsive {

    }

    .services-container {
        flex-direction: column;
        overflow: scroll;
        grid-template-columns: 1fr;
        top: 100px;
        max-height: calc(100% - 300px) !important;
    }

    
    .about-section {
        
        font-size: 13px;
    }
    .left-about {
        color: black;
        top: 110px;
        text-align: justify;
    }
    .left-about img {
        display: none;
    }

    .right-about img {
        width: 35px;
        height: 35px;
        margin-top: 0;
        margin-right: 20px;
        margin-left: 20px;

    }


    .right-about {
        margin-top: 30px !important;
        
    }
    
    

    .service-responsive {
        display: flex;
        opacity: 0;height: 10px;
        margin: 0;
    }
  }
  