.orders-details {
    width: calc(90% - 40px);
    height: calc(90% - 40px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    background-color: #F1EFEF;
    border-radius: 5px;
    position: relative;
    font-weight: bold;
}

.close-btn {
    z-index: 5;
    background-color: #F1EFEF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.top-orders-details,
.bottom-orders-details {
    width: 100%;
    height: 50%;
    position: relative;
}

.top-orders-details {
    background-color: rgba(54, 47, 217 , 0.7);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.orders-infos {
    width: calc(18% - 40px);
    height: calc(80% - 40px);
    background-color: white;
    border-radius: 5px;
    padding: 20px;
    position: relative;
}

.orders-infos:first-child {
    background-color: transparent;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.orders-infos:first-child p {
    margin-bottom: 20px;
}

.orders-infos:nth-child(5) .order-info-img {
    background-color: #00A9FF;
}


.order-info-header {
    width: 100%;
    display: flex;
    align-items: center;
}

.order-info-header p {
    width: 80%;
}

.order-info-img {
    min-width: 50px;
    min-height: 50px;
    background-color: rgba(54, 47, 217 , 1);
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;

}

.order-info-corps {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.order-info-corps p+p {
    font-size: 20px;
    margin-top: 10px;
}


.bottom-orders-details {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
    font-weight: bold;
}


.bottom-orders-details .orders-infos {
    width: 12% ;
    height: 70%;
    border-radius: 5px;
    background-color: white;

    
}

.bottom-orders-details .orders-infos:first-child {
    box-shadow: none;
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    color: #212A3E;
}







.bottom-orders-details .state-container p {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}



.big-text {
    font-size: 20px;
}


.bottom-orders-details .order-info-img {
    width: 50px;
    height: 50px;
    position: absolute;
    left: 50%;
    top: -25px;
    transform: translateX(-50%);
}

.bottom-orders-details .orders-infos {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
}

.bottom-orders-details .order-info-corps {
    height: 60%;
}

@media only screen and (max-width: 700px) {
    .top-orders-details {
        flex-direction: column;
        justify-content: flex-start;
        overflow-y: auto;
        padding: 10px;
        box-sizing: border-box;
        margin-bottom: 15px;
    }

    .top-orders-details .orders-infos {
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        height: 80px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .top-orders-details .orders-infos + .orders-infos {
        margin-top: 10px;
    }

    .top-orders-details .order-info-corps {
        align-items: end;
    }

    .order-info-corps .small-text {
        font-size: small;
        text-align: right;
        text-overflow: ellipsis;
    }

    .order-info-corps p:last-child {
        font-size: smaller;
        text-overflow: ellipsis;
    }

    .orders-infos:first-child {
        flex-direction: column;
        height: 130px;
    }

    .bottom-orders-details {
        flex-direction: column;
        justify-content: flex-start;
        overflow-y: auto;
        padding: 10px;
        box-sizing: border-box;
    }

    .bottom-orders-details .orders-infos,.bottom-orders-details .orders-infos:first-child {
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        height: 80px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .bottom-orders-details .orders-infos:first-child {
        gap: 15px;
    }

    .bottom-orders-details .orders-infos + .orders-infos {
        margin-top: 10px;
    }

    .bottom-orders-details .order-info-img {
        position: relative;
        transform: none;
        left: 0;
        top: 0;
    }

    .bottom-orders-details .order-info-corps {
        align-items: end;
    }

    .bottom-orders-details .orders-infos > p {
        text-align: left;
    }
}

.ellipsis {
}

.orders-infos img {
    width: 25px;

}

.mirror {
    -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}